import React, { useState, useEffect } from 'react'

const Counter = ({ range }) => {
  const [counter, setCounter] = useState(0)

  useEffect(() => {
    const interval = setInterval(() => {
      if (counter < parseInt(range)) {
        setCounter(counter + 1)
      }
    }, 1)
    return () => clearInterval(interval)
  }, [counter])
  return counter

  
}

export default Counter
