import React from 'react'
import { Link } from 'react-router-dom'
import styled from 'styled-components'
import cuetlogo from '../assets/images/cuet-img/cuetlogo.png'
const Footer = ({ title }) => {
    return (
        <Wrapper>
           
                <div className="container">
                    <div className="row">
                        <div className="col-4">
                            <ul className='cuet-links'>
                                <li><Link>CUET Library</Link></li>
                                <li><Link>consumer information</Link></li>
                                <li><Link>webmail</Link></li>
                                <li><Link>consumer information</Link></li>
                                <li><Link>Human resources</Link></li>
                                <li><Link>conatct us</Link></li>
                                <li><Link>Faculty and staff</Link></li>
                            </ul>
                        </div>
                        <div className="col-4  text-center">
                            <div className="copyright">
                                <img src={cuetlogo} alt="cuet logo" />
                                <h4>{title}, Chittagong - 4349</h4>
                                &copy; {new Date().getFullYear()} .All rights reserved CUET
                            </div>

                        </div>
                        <div className="col-4 third-col">
                            <h4 className=''>my cuet</h4>
                            <ul className='social-links'>
                                <li><Link><i className="fab fa-instagram fa-2x"></i></Link></li>
                                <li><Link><i className="fab fa-facebook fa-2x"></i></Link></li>
                                <li><Link><i className="fab fa-linkedin fa-2x"></i></Link></li>
                                <li><Link><i className="fab fa-youtube fa-2x"></i></Link></li>
                            </ul>
                            <ul className="connect">
                                <li>
                                    <i className="fas fa-envelope"></i> iict@cuet.ac.bd, registrar@cuet.ac.bd
                                    <i className="fas fa-phone"></i> +880-31-714946
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
          
        </Wrapper>
    )
}

const Wrapper = styled.section`
    .cuet-links li{
        margin-bottom: 5px;
       
    }
    .cuet-links li a{
        transition: all 0.3s ease;
        color: var(--clr-primary-2);
        border-bottom:1px solid var(--clr-primary-1);
        text-align: start;
    }
    .cuet-links li a:hover{
       font-size: 20px;
       border-bottom:0;
    }
    .copyright{
        color: var(--clr-primary-2);
       
    }

    .copyright img{

        width: 100px;
        height: 100px;
        margin: 0 auto;
        padding-bottom: 5px;
    }
    .third-col h4{
        text-align: center;
        font-size: 20px;
        color: var(--clr-primary-2);
        font-weight: 700;
    }

    .social-links{
        display: flex;
        justify-content: center;
        padding-left: 0;
    }
    .social-links li{
        border-radius: 0;
        margin: 5px;
        padding: 0;
    }

    .social-links li a{
        padding: 0;
       font-size: 25px;
       color: var(--clr-primary-2);
       border-radius: 0;
       transition: all 0.3s linear;
    }


    .connect li{
        color: var(--clr-primary-2);
    }

    
`

export default Footer
