import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import sliderData from '../assets/sliderData'
import Carousel from 'react-bootstrap/Carousel'
import { Link } from 'react-router-dom'
import departments from '../assets/departments'
import banner2 from '../assets/images/cuet-img/banner-2.jpg'
import news from '../assets/news'
import Counter from '../components/Counter'
import aboutInfo from '../assets/aboutInfo'


const Home = () => {



  return (
    <Wrapper>
      <Carousel
        slide='false'
        fade='true'>
        {
          sliderData.map(item => (
            <Carousel.Item
              key={item.id}
              interval={3000}
            >
              <img
                className={item.id % 2 === 0 ? 'clip1' : 'clip2'}
                src={item.img}
                alt={item.title}

              />
              <Carousel.Caption>
                <h1>{item.title}</h1>
                <p>{item.desc}</p>
                <button className='btn'>see more</button>
              </Carousel.Caption>
            </Carousel.Item>
          ))
        }
      </Carousel>

      <section className="covid-update">
        <Link><h3>Coronavirus Updates (COVID-19) <i className="fas fa-chevron-right"></i></h3></Link>
      </section>


      <section className="departments text-center">
        <h2>faculty and  departments </h2>
        <div className="btn-group mb-5">
          <button className='btn mx-1'>all faculties</button>
          <button className='btn mx-1'>all departments</button>
        </div>

        <div className="lists">
          {
            departments.map(dept => (

              <div key={dept.id} className="panel" style={{ backgroundImage: `url(${dept.img})` }}>

                <h3>{dept.title}</h3>
                <p>{dept.desc}</p>
                <Link><span>see more</span></Link>

              </div>
            ))
          }

        </div>
      </section>


      <section className="news text-center">
        <h2>news and  events </h2>
        <div className="btn-group mb-5">
          <button className='btn mx-1'>all news</button>
          <button className='btn mx-1'>all events</button>
        </div>

        <div className="container">
          <div className="row justify-content-md-center">
            {
              news.map(item => (
                <div className="col-md-4" key={item.id}>
                  <div className="card">
                    <img src={item.img} alt={item.title} />
                    <Link><div class="overlay">{item.desc.substring(0, 25)}...</div></Link>
                  </div>
                </div>
              ))
            }

          </div>
        </div>

      </section>


      <section className="about text-center">
        <h2>about cuet</h2>
        <div className="btn-group mb-5">
          <button className='btn mx-1'>visit campus</button>
        </div>

        <div className="counter-sec">
          {
            aboutInfo.map(data => (
              <div className="counter-container" key={data.id}>
                <div className="counter"><Counter range={data.amount} /></div>
                <span>{data.title}</span>
              </div>
            ))
          }

        </div>

      </section>

    </Wrapper>
  )
}

const Wrapper = styled.article`


.clip-c{
  clip-path: polygon(68% 0, 20% 18%, 20% 55%, 20% 80%, 71% 100%, 50% 100%, 0 100%, 0% 70%, 0% 35%, 0 0);
}
.clip-u{
  clip-path: polygon(24% 78%, 78% 79%, 79% 0, 100% 0, 100% 100%, 50% 100%, 0 100%, 0% 70%, 0 0, 24% 0);
}
.clip-e{
  clip-path: polygon(30% 0%, 100% 0, 23% 30%, 100% 49%, 24% 66%, 100% 100%, 0 100%, 0 0);
}
.clip-t{
  clip-path: polygon(50% 0%, 100% 0, 100% 26%, 64% 26%, 64% 68%, 64% 100%, 35% 100%, 34% 24%, 0 23%, 0 0);
}



 /* slider  */
 .carousel-item{
   opacity: 0 !important;
   transition: var(--transition);
 }
 
 .active.carousel-item{
   opacity: 1 !important;
   transition: var(--transition);
 }

 .carousel-indicator, .carousel-control-prev,.carousel-control-next {
   display: none;
 }
  .carousel-caption{
    
    /* border:5px solid green; */
    top: 20%;
    left: 0;
    width: 50%;
    padding: 10px;
  }
  .carousel-caption h1{
    white-space: normal;
    color: var(--clr-primary-2);
    font-weight: 800;
    padding: 10px 0;
    
  }
  .carousel-caption p{
      color: var(--clr-primary-1);
      text-align: start;
      font-size: 20px;
      text-transform: capitalize;
      padding: 20px;
      
      
  }
  .carousel-item img{
    /* border:5px solid red; */
    height: 500px;
    width: 40%;
    float: right;
    transition: var(--transition);
  }
  .active.carousel-item img{
    /* border:5px solid red; */
    height: 500px;
    width: 50%;
    float: right;
    transition: var(--transition);
  }

  .clip1{
    
    clip-path: polygon(100% 0%, 75% 50%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
  }
  .clip2{
    clip-path: polygon(75% 0%, 100% 50%, 75% 100%, 0% 100%, 25% 50%, 0% 0%);
  }
  
  /* end slider */

/* covid update */
  .covid-update h3{
      background-color: var(--clr-secondary-1);
      padding: 10px;
      color: var(--clr-primary-1);
  }

  /* departments section */
.departments{
  height: 100vh;
  margin: 40px 10px;
}
.departments h2{
  padding: 30px 10px;
  color: var(--clr-primary-2);
  font-weight: 900;
  font-size: 50px;
  text-shadow: 2px 2px 4px #000000;
  text-transform: uppercase;
}
.lists{
  display: flex;
  justify-content: center;
  /* position: relative;
  left: -50%; */
  /* width: 100vw; */
  }

.panel{
  background-size:auto 100%;
  background-position: center;
  background-repeat: no-repeat;
  border-radius:50px;
  flex: 0.5;
  margin:10px;
  position: relative;
  /* left: -50%; */
  height: 50vh;
  transition: all 0.3s ease-in;
  cursor: pointer;
  color: var(--clr-primary-10);
}

.panel h3{
  text-align: center;
  position: absolute;
  margin: 0;
  width: 100%;
  left: 0%;
  top: 50%;
  transition: all 0.6s ease-in;
  -webkit-text-stroke: 1px var(--clr-primary-2);
  -webkit-text-fill-color: var(--clr-secondary-1);
  overflow: hidden;
  font-weight: 700;
}
.panel p{
  text-align: center;
  position: absolute;
  margin: 0;
  padding: 5px;
  top: 50%;
  width: 100%;
  left: 0%;
  transition: all 0.6s ease-in;
  opacity: 0;
  color: var(--clr-primary-9);
  text-align: center;
}

.panel a{
  position: absolute;
  top: 60%;
  width: 100%;
  left: 0%;
  transition: all 0.6s ease-in;
  color: var(--clr-primary-10);
}
.panel:hover{
  flex:1.5;
  border-radius:0;
  transition: all 0.6s ease-in;
}
.panel:hover h3{
  top: 5%;
  transition: all 0.6s ease-in;
  
}
.panel:hover p{
  top: 50%;
  transition: all 0.6s ease-in;
  /* left: 35%; */
  opacity: 1;
}
.panel:hover a{
  top: 80%;
  transition: all 0.6s ease-in;
  /* left: 35%; */
}
.panel:hover:before{
  border-radius:0;
  transition: all 0.6s ease-in;
}
.panel::before {
  content: '';
  position: absolute;
  border-radius:50px;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  /* z-index: 1; */
  background: rgba(0, 0, 0, .5);
  transition: all 0.6s ease-in;
}
/* end departments */


/* news and events */
.news{
  /* height: 100vh; */
  text-transform: capitalize;
  margin: 40px 10px;
  background-color: var(--clr-primary-10);
}
.news h2{
  padding: 30px 10px;
  color: var(--clr-primary-2);
  font-weight: 900;
  font-size: 50px;
  text-shadow: 2px 2px 4px #000000;
  text-transform: uppercase;
}
.card {
  position: relative;
  /* padding: 10px; */
  margin: 10px;
  /* width: 50%; */
  /* max-width: 300px; */
}
.card:hover img{
  transform: scale(1.1);
}

.news img {
  transition:all 0.6s ease;
  display: block;
  width: 100%;
  height: 200px;
  box-shadow: 5px 10px 18px #888888;
  border-radius: 0;
  border-bottom:5px solid var(--clr-secondary-1);
}

.overlay {
  position: absolute; 
  bottom: 0; 
  background: var(--clr-primary-2);
  background: rgba(0, 0, 0, 0.5); 
  color:var(--clr-primary-10);
  width: 100%;
  transition: 0.6s ease;
  opacity:0;
  color: white;
  font-size: 20px;
  padding: 20px;
  text-align: center;
}

.card:hover .overlay {
  opacity: 1;
}
/* end news and events */


/* about*/
.about{
  /* height: 100vh; */
  text-transform: capitalize;
  margin: 40px 10px;
  background-image: url(${banner2});
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  border-bottom:5px solid var(--clr-secondary-1);
  min-height: 500px;
  position: relative;
  z-index: 0;
    &:before {
        background: rgba(0, 0, 0, 0.6);
        content: "";
        height: 100%;
        left: 0;
        position: absolute;
        top: 0;
        width: 100%;
        z-index: -1;
    }
}
.about h2{
  padding: 30px 10px;
  color: var(--clr-primary-10);
  font-weight: 900;
  font-size: 50px;
  text-shadow: 2px 2px 4px var(--clr-primary-2);;
  text-transform: uppercase;
}

.counter-sec{
  display: flex;
  color: var(--clr-primary-10);
  align-items: center;
  justify-content: center;
  margin: 0;
}

.counter-container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 30px 50px;
}
.counter{
  font-size: 60px;
  margin-top:10px;
}
/* end about */

`

export default Home
